  }
}
@media (min-width: 1800px) {
  .calculation__description {
    background-position: center bottom 20px;
  }
}
.calculation__btn {
  margin: 0 auto;
}
.services-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px 15px;
}
@media (min-width: 768px) {
  .services-list {
    gap: 19.5px;
  }
}
@media (min-width: 1280px) {
  .services-list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.services-list__item {
  width: 100%;
  min-width: 150px;
  min-height: 60px;
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  text-align: center;
  color: var(--color-font-dark);
  background-color: #ffffffb5;
}
@media (min-width: 1280px) {
  .services-list__item {
    width: 160px;
  }
}
@media (min-width: 1800px) {
  .services-list__item {
    width: 100%;
  }
}
.services-list__item:hover {
  background-color: var(--color-accent);
}
.intro {
  position: relative;
  padding-top: 46px;
  padding-bottom: 63px;
  color: var(--color-font-light);
  background-color: var(--color-black-03);
  background-image: url("../../images/intro/intro-mobile.webp");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.intro::after {
  position: absolute;
  content: "";
  inset: 0;
  background: linear-gradient(200.02deg, rgba(18, 5, 1, 0.7) 22.86%, rgba(30, 8, 1, 0.07) 44.88%, rgba(11, 10, 9, 0.31) 75.31%);
}
@media (min-width: 768px) {
  .intro {
    padding-top: 110px;
    padding-bottom: 132px;
    background-image: url("../../images/intro/intro-tablet.webp");
  }
}
@media (min-width: 1280px) {
  .intro {
    padding-top: 90px;
    padding-bottom: 90px;
    background-image: url("../../images/intro/intro-laptop.webp");
  }
}
@media (min-width: 1800px) {
  .intro {
    padding-top: 166px;
    padding-bottom: 243px;
    background-image: url("../../images/intro/intro-desktop.webp");
  }
}
.intro__wrapper {
  position: relative;
  z-index: 10;
  display: grid;
  gap: 16px;
}
@media (min-width: 768px) {
  .intro__wrapper {
    grid-template-columns: 1fr min-content;
    gap: 17px 29px;
  }
}
@media (min-width: 1280px) {
  .intro__wrapper {
    grid-template-rows: min-content min-content 1fr min-content;
    gap: 15px 50px;
  }
}
@media (min-width: 1800px) {
  .intro__wrapper {
    gap: 15px 75px;
  }
}
.intro__title {
  font-size: 60px;
  line-height: 100%;
  font-weight: 900;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 0 28px var(--color-black-01);
}
@media (min-width: 768px) {
  .intro__title {
    grid-column: 1 / -1;
    text-align: left;
  }
}
@media (min-width: 1280px) {
  .intro__title {
    grid-column: 1 / 2;
  }
}
@media (min-width: 1800px) {
  .intro__title {
    line-height: 117%;
  }
}
.intro__title span {
  color: var(--color-accent);
}
.intro__subtitle {
  margin-bottom: 3px;
  font-size: 24px;
  line-height: 117%;
  text-align: center;
  text-shadow: 0 0 28px var(--color-black-01);
}
@media (min-width: 768px) {
  .intro__subtitle {
    grid-column: 1 / -1;
    margin-bottom: 1px;
    font-size: 42px;
    text-align: left;
  }
}
@media (min-width: 1280px) {
  .intro__subtitle {
    grid-column: 1 / 2;
    margin-bottom: 0;
    font-size: 42px;
  }
}
.intro__description {
  order: 1;
  max-width: 335px;
  margin: 0 auto;
  margin-bottom: 11px;
  text-align: center;
  text-shadow: 0 0 28px var(--color-black-01);
}
@media (min-width: 768px) {
  .intro__description {
    grid-column: 1 / 2;
    order: unset;
    max-width: unset;
    margin: 0;
